import React  from 'react';
import emailjs from 'emailjs-com';
import "./contact.css"

export default function ContactForm() {

  function sendEmail(e) {
    e.preventDefault();
    alert("The message was sent !!")
    emailjs.sendForm('service_nei35fq', 'template_r456brg', e.target, 'user_mQjvn8Cb8xojCg7tJnfqb')
      .then((result) => {
          console.log(result.text);
      }, (error) => {
          console.log(error.text);
      });
  }
 
  return (
    <form className="contact-form" onSubmit={sendEmail}>
     

      
      <input className="infobox" type="text" name="user_name"   placeholder='Name'/>
      
      <input className="infobox" type="email" name="user_email" placeholder='Email' />
      
      <textarea className="mesbox" name="message" placeholder='Message'  />
      <input className="sendbtn" type="submit" value="Send" />
    </form>
  );
}
 
