import React from 'react'
import './index.css'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    useParams
  } from "react-router-dom";




function Pofocard(props) {
    return (
        <div className="probox">
            <h1 className="protitle">{props.title}</h1>
           
           
            <a href={props.link} className="prolink" >GO</a>
        </div>
    )
}

export default Pofocard



