import React, { Component } from "react";
import hku from './hku.jpg';
import Poly from './Poly.jpg'
import pku from  './pku.png'

 
class Education extends Component {
  render() {
    return (
      <div className="edu">
          <div className= "edubox1">

         
            <img className="eduicon" src={hku}/>
            <h2>Bachelor of Electrical Engineering </h2>

            <h3>The Uiversity of Hong Kong </h3>
            <h3>From 09/2018 - in progress </h3>
            
            <br/><br/>
            <a className="botton" href="https://engg.hku.hk/Teaching-Learning/BEng-BASc/Academic-Programmes">More details</a>
          </div>



          <div className= "edubox1">

        
          <img className="eduicon" src={pku}/> 
          <h2>2019 Globex Julmester</h2>
         
          <h3>Peking University</h3>
          <h3>From 06/2019 - 07/2019 </h3>
          <br/><br/><br/>
          <a className="botton" href="https://globex.coe.pku.edu.cn/">More details</a>
          
        </div>



          <div className= "edubox1">

          
          <img className="eduiconpoly" src={Poly}/>
          <h2>HD in Building Services Engineering </h2>
          
          <h3>The Polytechnic University of Hong Kong</h3>
          <h3>From 09/2016 - 08/2018 </h3>
          
          <a className="botton" href="https://www.bse.polyu.edu.hk/programmes/Prog_Leaflet/HD_2020_v4.pdf">More details</a>
        </div>

        
      



          
        </div>
      
      
    );
  }
}
 
export default Education;